import React, { useState } from "react";
import "./Profile.css";

export default function Profile({ user }) {
  //    
  if (!user) return null;
  return (
    <div className="profile-main">
      <div className="profile-card">
        <img src="/user-placeholder.png" alt=" " className="profile-img" />
        <h3>{user.fullName}</h3>
        <p className="profile-role">{user.type === "patient" ? "" : user.type === "provider" ? user.providerType : ""}</p>
        <div className="profile-info">
          <div>: {user.address}</div>
          <div> : {user.phone}</div>
          <div> : {user.email}</div>
          <div>: {user.age}</div>
          <div>: <img src="/id-card.png" alt=" " style={{height: 32, verticalAlign: "middle"}}/></div>
        </div>
      </div>
    </div>
  );
}