import React, { useState } from "react";
import "./LoginRegister.css";

export default function LoginRegister({ setUser }) {
  const [isRegister, setIsRegister] = useState(false);
  const [form, setForm] = useState({
    fullName: "",
    age: "",
    address: "",
    phone: "",
    email: "",
    password: "",
    type: "patient",
    providerType: "",
    idCard: null,
    degree: null,
    license: null,
  });
  const [loading, setLoading] = useState(false);

  function handleChange(e) {
    const { name, value, files } = e.target;
    if (files) {
      setForm((prev) => ({ ...prev, [name]: files[0] }));
    } else {
      setForm((prev) => ({ ...prev, [name]: value }));
    }
  }

  function handleSubmit(e) {
    e.preventDefault();
    setLoading(true);

    if (isRegister) {
      //   
      fetch("http://localhost:5000/users", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(form),
      })
        .then((res) => res.json())
        .then((user) => {
          setLoading(false);
          setUser(user);
        });
    } else {
      //  :     
      fetch(
        `http://localhost:5000/users?email=${form.email}&password=${form.password}`
      )
        .then((res) => res.json())
        .then((users) => {
          setLoading(false);
          if (users.length > 0) {
            setUser(users[0]);
          } else {
            alert("   ");
          }
        });
    }
  }

  return (
    <div className="login-register-bg">
      <div className="login-register-card">
        <h2>{isRegister ? "  " : " "}</h2>
        <form onSubmit={handleSubmit} className="login-register-form">
          {isRegister && (
            <>
              <input
                type="text"
                name="fullName"
                placeholder=" "
                value={form.fullName}
                onChange={handleChange}
                required
              />
              <input
                type="number"
                name="age"
                placeholder=""
                value={form.age}
                onChange={handleChange}
                required
              />
              <input
                type="text"
                name="address"
                placeholder=""
                value={form.address}
                onChange={handleChange}
                required
              />
              <input
                type="text"
                name="phone"
                placeholder="  "
                value={form.phone}
                onChange={handleChange}
                required
              />
            </>
          )}
          <input
            type="email"
            name="email"
            placeholder=" "
            value={form.email}
            onChange={handleChange}
            required
          />
          <input
            type="password"
            name="password"
            placeholder=" "
            value={form.password}
            onChange={handleChange}
            required
          />
          {isRegister && (
            <>
              <select name="type" value={form.type} onChange={handleChange}>
                <option value="patient"></option>
                <option value="provider">  </option>
              </select>
              {form.type === "provider" && (
                <>
                  <select
                    name="providerType"
                    value={form.providerType}
                    onChange={handleChange}
                    required
                  >
                    <option value=""> </option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value="  ">  </option>
                    <option value=""></option>
                    <option value=" "> </option>
                    <option value=" "> </option>
                    <option value=" "> </option>
                  </select>
                  <label>  :</label>
                  <input
                    type="file"
                    name="license"
                    accept="image/*"
                    onChange={handleChange}
                    required
                  />
                  <label>  :</label>
                  <input
                    type="file"
                    name="degree"
                    accept="image/*"
                    onChange={handleChange}
                    required
                  />
                </>
              )}
              <label>  :</label>
              <input
                type="file"
                name="idCard"
                accept="image/*"
                onChange={handleChange}
                required
              />
            </>
          )}
          <button type="submit" disabled={loading}>
            {loading ? " ..." : isRegister ? " " : ""}
          </button>
        </form>
        <div className="login-register-switch">
          {isRegister ? (
            <span>
               {" "}
              <button onClick={() => setIsRegister(false)}> </button>
            </span>
          ) : (
            <span>
                {" "}
              <button onClick={() => setIsRegister(true)}>  </button>
            </span>
          )}
        </div>
      </div>
    </div>
  );
}